@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,100,500);
@import url(https://fonts.googleapis.com/css2?family=Handjet:wght@500&display=swap);

body,
html {
  margin: 0;
  height: 100%;
  font-family: 'Handjet';
  background: url('/src/img/pixelbackgrounf.png');
  background-size: 100%;
}

.wrapper {
  animation: scroll 100s linear infinite;
  background: url("/src/img/pixelbackgrounf.png"),
    RGB(103, 171, 239);
  color: #eee;
  height: 100vh;
  background-size: 120%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

@keyframes scroll {
  100% {
    background-position: -400% 0px;
  }
}

input {
  border: none;
}

button:focus {
  outline: none;
}

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.65);
}

::-webkit-input-placeholder .input-line:focus+::input-placeholder {
  color: #fff;
}

.highlight {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 400;
  cursor: pointer;
  transition: color .2s ease;
}

.highlight:hover {
  color: #fff;
  transition: color .2s ease;
}

.spacing {
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  height: 120px;
  font-weight: 300;
  text-align: center;
  margin-top: 10px;
  color: rgba(255, 255, 255, 0.65)
}

.input-line:focus {
  outline: none;
  border-color: #fff;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.ghost-round {
  cursor: pointer;
  background: none;
  border: 1px solid rgba(255, 255, 255, 0.65);
  color: rgba(255, 255, 255, 0.65);
  -webkit-align-self: flex-end;
  -ms-flex-item-align: end;
  align-self: flex-end;
  font-size: 19px;
  font-size: 1.2rem;
  font-family: 'Handjet';
  font-weight: 300;
  line-height: 2.5em;
  margin-top: auto;
  margin-bottom: 25px;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.ghost-round:hover {
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.input-line {
  background: none;
  margin-bottom: 10px;
  line-height: 2.4em;
  color: #fff;
  font-family: 'Handjet';
  font-weight: 300;
  letter-spacing: 0px;
  letter-spacing: 0.02rem;
  font-size: 19px;
  font-size: 1.2rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.65);
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.full-width {
  width: 100%;
}

.clicker__container {
  border: 1px solid rgba(255, 255, 255, 0.65);
  margin: 0 auto;
  width: 1000px;
  height: 1000px;
  border: 1px black;
  min-width: 1000px;
}

.clicker__stats{
  border: 5px solid #EDBB43;
  background: #edd143;
  color:#000;
  width: 300px;
  margin: 0 auto;
  font-size: 25px;
  position: relative;
 
}
.nav{
  display: inline;
}
.left{
  position: relative;
  bottom: 70px;
  display: flex;
  justify-content: right; 
 
}

.clicker__cirle img {
  filter: drop-shadow(20px 20px 25px rgb(100, 118, 115));
  animation: shake 0.35s ease-in-out;
  display: flex;
  margin: 0 auto;
  position: relative;
  left: 1%;

}

.clicker__cirle img:hover {
  filter: drop-shadow(20px 20px 25px rgb(100, 118, 115));
  animation: shake 0.35s ease-in-out;
  display: flex;
  margin: 0 auto;
}

.level__count {
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  right: 2%;
  color: black;
}


@keyframes shake {

  0%,
  100% {
    transform: translateX(0);
  }

  20% {
    transform: translateX(-13px);
  }

  40% {
    transform: translateX(13px);
  }

  60% {
    transform: translateX(-8px);
  }

  80% {
    transform: translateX(8px);
  }
}

.clicker_money {
  width: 100px;
  height: 50px;
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  right: 2%;
  border: 5px solid #EDBB43;
  background: #edd143;
}

.clicker_money img {
  margin-right: 5px;
  width: 35px;
}

.clicker_money h1 {
  margin-right: 5px;
}

.clicker__upgrade {
  display: inline-flex;
  text-align: justify;
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.but__upgrade {
  font-family: 'Handjet';
  width: 100px;
  height: 50px;
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 5px solid #EDBB43;
  background: #edd143;
  width: 120px;
  height: 60px;
  font-size: 25px;
  font-weight: bolder;
  margin: 5px;
}
.main__text{
  font-size: 20px;
  color:white;

}

.but__upgrade:hover,
.clicker__level:hover,
.clicker_money:hover {
  border: 5px solid #ed5f43;
}


.clicker__level {
  width: 100px;
  height: 50px;
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 5px solid #EDBB43;
  background: #edd143;
  width: 120px;
  height: 60px;
  font-size: 25px;
  font-weight: bolder;
  margin: 5px;
}



.input-fields {
  margin-top: 25px;
}

.container {
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  background: rgb(103 ,171, 289);
  height: 100%;
}

.content {
  padding-left: 25px;
  padding-right: 25px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  font-family: 'Handjet';
  display: flex;
  -webkit-flex-flow: column;
  -ms-flex-flow: column;
  flex-flow: column;
  z-index: 5;
}

.welcome {
  font-weight: 200;
  margin-top: 150px;
  text-align: center;
  font-size: 40px;
  font-size: 2.5rem;
  letter-spacing: 0px;
  letter-spacing: 0.05rem;
}

.subtitle {
  text-align: center;
  line-height: 1em;
  font-weight: 100;
  letter-spacing: 0px;
  letter-spacing: 0.02rem;
}

.subtitle_decoration{
  text-decoration:underline ;
}

.menu {
  background: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 50px;
}

.window {
  z-index: 100;
  color: #fff;
  font-family: 'Handjet';
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-flow: column;
  -ms-flex-flow: column;
  flex-flow: column;
  box-shadow: 0px 15px 50px 10px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  height: 100vh;
  width: 360px;
  background: #fff;
  background: url('https://pexels.imgix.net/photos/27718/pexels-photo-27718.jpg?fit=crop&w=1280&h=823') top left no-repeat;
}

.overlay {
  height: 560px;
  position: absolute;
  width: 360px;
  z-index: 1;
}

.welcome__container h1 {
  font-size: 100px;
  color: white;
  text-shadow: 3px 0 3px #000,
    0 3px 3px #000,
    -3px 0 3px #000,
    0 -3px 3px #000;
    position: relative;
    display: flex;
    margin: 0 auto;
    flex-direction: row;
    justify-content: center;
    align-items: center; 
    margin-bottom: 40px;

}

.welcome__container h2 {
  font-size: 80px;
  text-shadow: 3px 0 3px #000,
    0 3px 3px #000,
    -3px 0 3px #000,
    0 -3px 3px #000;
    position: relative;
    display: flex;
    margin: 0 auto;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}

.welcome__container h3 {
  font-size: 70px;
  text-shadow: 3px 0 3px #000,
  0 3px 3px #000,
  -3px 0 3px #000,
  0 -3px 3px #000;
    position: relative;
    display: flex;
    margin: 0 auto;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}
.welcome__container img {
  width: 200px;
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.welcome__container{
  max-width: 1000px;
  margin: 0 auto;
  margin-top: 45px;
}
.button__container{
  position: relative;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.border{
  border: 2px solid black;
  width: 120px;
  height: 50px;
  background: white;
  color: black;
  margin: 10px;
}
.border:hover{
  border: 5px solid black;

}

.border p{
  text-decoration: none;
  color: black;
  font-size: 25px;
  font-weight: 800;
}




@media (max-width: 500px) {
  .window {
    width: 100%;
    height: 100%;
  }

  .overlay {
    width: 100%;
    height: 100%;
  }
}


@media all and (min-width: 1024px) and (max-width: 1280px) { 
  .clicker__container {

    min-width: 1024px;
    max-width: 1280px;
    position: relative;
    right: 4vh;
  }
}

@media all and (min-width: 768px) and (max-width: 1024px) { 
  .clicker__container {

    min-width: 768px;
    max-width: 1024px;
    position: relative;
    right: 4vh;
  }
}

@media all and (min-width: 480px) and (max-width: 768px) {
  .clicker__container {
    min-width: 480px;
    max-width: 768px;
    position: relative;
    right: 4vh;
  }
 }

@media all and (max-width: 480px) {
  .clicker__container {
    width: 480px;
    height: 480px;
    min-width: 480px;
    position: relative;
    right: 4vh;
  }

 }